import React, { useEffect, useState, useRef } from 'react';
import { Link } from 'react-router-dom';
import './../Text.css';

import './../App.css';

import { ReactComponent as Logo } from './../assets/logo.svg';
import { ReactComponent as LogoDark } from './../assets/logo-dark.svg';

function Navbar() {
  const navbar = useRef();
  const link1 = useRef();
  const link2 = useRef();
  const link3 = useRef();
  const link4 = useRef();
  const link5 = useRef();
  const darklogo = useRef();
  const lightlogo = useRef();

  useEffect(() => {
    const changeBg = () => {
      if (window.scrollY > 10) {
        navbar.current.style.backgroundColor = 'white';
        navbar.current.style.transition = '0.25s ease-in-out';
        navbar.current.style.borderBottom = '1px #ccc solid';
        link1.current.style.color = 'black';
        // link2.current.style.color = 'black';
        link3.current.style.color = 'black';
        // link4.current.style.color = 'black';
        // link5.current.style.color = 'black';
        darklogo.current.style.display = 'block';
        lightlogo.current.style.display = 'none';
      } else if (window.scrollY < 10) {
        navbar.current.style.backgroundColor = 'rgba(0,0,0,0)';
        navbar.current.style.transition = '0.25s ease-in-out';
        navbar.current.style.borderBottom = 'none';
        link1.current.style.color = 'white';
        // link2.current.style.color = 'white';
        link3.current.style.color = 'white';
        // link4.current.style.color = 'white';
        // link5.current.style.color = 'white';
        darklogo.current.style.display = 'none';
        lightlogo.current.style.display = 'block';
      }
    };

    document.addEventListener('scroll', changeBg);

    return () => {
      document.removeEventListener('scroll', changeBg);
    };
  }, []);

  return (
    <div className="nav" ref={navbar}>
      <Logo className="logo" ref={lightlogo} />
      <LogoDark className="dark-logo" ref={darklogo} />
      <div className="nav-menu">
        <Link to="/" className="nav-item" ref={link1}>
          Work
        </Link>
        {/* <Link to="/archives" className="nav-item" ref={link2}>
          Archive
        </Link> */}
        {/* <Link to="/lab" className="nav-item" ref={link3}>
          Lab
        </Link> */}
        <Link to="/about" className="nav-item" ref={link3}>
          About
        </Link>
        {/* <Link to="/resume" className="nav-item" ref={link5}>
          Resume
        </Link> */}
      </div>
    </div>
  );
}

export default Navbar;
