import React from 'react';
import { useLocation } from 'react-router-dom';
import './../Text.css';
import './../App.css';
import './../Text.css';
import projectData from './../projectData';
import MoreProjectCard from './MoreProjectCard';

function MoreProjects() {
  const { pathname } = useLocation();

  const moreProjects = projectData
    .filter(({ link }) => link !== pathname)
    .map((project) => (
      <MoreProjectCard
        id={project.name}
        key={project.name}
        link={project.link}
        title={project.title}
        description={project.description}
      />
    ));

  return (
    <div className="section-100">
      <h3 className="mb-50 padding-h">View other projects</h3>
      {moreProjects}
    </div>
  );
}

export default MoreProjects;
